import React from "react"
import ParalaxBackground from "./ParalaxBackground"
import Resume from "./Resume"
import Projects from "./Projects"
import ScrollableAnchor from 'react-scrollable-anchor'

class HomePage extends React.Component{
	constructor(){
		super()
		this.state = {
			links: []
		}
		this.setLinks = this.setLinks.bind(this)
	}
	setLinks(newLinks){
		if(this.state.links.length === 0){
			this.setState({links: newLinks})
		}
	}
	render(){
		let linkDisplay = this.state.links.map((link) =>{
			return(
				<li className="anchor-link">
					{link}
				</li>
			)
		})
		return(
			<div className="homepage">
				<ParalaxBackground />
				<div className="content-container">
					<div className="content">
						<div className="profile">
							<div className="prof-pic-container">
								<img src={require('../Assets/prof-pic.jpg')} className="prof-pic" />
							</div>
							<h1 id="name">Joshua Rubin</h1>
							<div className="sidebar">
								<div className="about">
									<h3>About Me</h3>
									<p>
										I love applying my creativity to write clean and reusable code and working in a team to create awesome projects. I am a full-stack Software engineer from San Francisco, specializing in backend development, creating database schema and RESTful APIs in NodeJS.  
									</p>
								</div>
								<div className="sticky-sidebar">
									<ul>
										<li className="anchor-link">
											<a href='#resume'> Resume </a>
										</li>
										<li className="anchor-link">
											<a href='#projects'> Projects </a>
										</li>
										<ul>
											{linkDisplay}
										</ul>
									</ul>
								</div>
							</div>
						</div>
						<div className="main">
							<ScrollableAnchor id ='resume'>
								<Resume />
							</ScrollableAnchor>
							<ScrollableAnchor id ='projects'>
								<Projects setLinks = {this.setLinks}/>
							</ScrollableAnchor>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default HomePage